main {
   margin: 0 auto;
   max-width: 85%;
   padding: 2px 20px;
}
:root {
   --primary: #dab33e;
   --secondary: #7efff0;
   --tertiary: #4caf50;
   --light: white;
   --dark: #0232f4;
   --darker: #010b1b;
   --alpha: rgba(138, 85, 85, 0.8);
   --error: #f44336;
   --spacing-one: calc(1rem * 0.8);
   --spacing-two: calc(2rem);
   --spacing-five: calc(2.2rem);
}

* {
   box-sizing: border-box;
}

html,
body {
   min-height: 100%;
}

body {
   margin: 0;
   padding: 0;
   font-weight: 200;
   line-height: 1.5;
   font-size: 24px;
   letter-spacing: 0.1em;
   background: linear-gradient(#d4d4e5, #04222e);
   color: white;
   color: var(--light);
   font-family: "Open Sans", sans-serif;
}

h1,
h2,
h3,
h4,
h5,
h6 {
   font-weight: 200;
   margin: 4px 0;
   line-height: 1.25;
}

h1 {
   font-size: 3.5rem;
   margin: 2px 50px;
   text-shadow: 5px 5px 10px rgb(2, 6, 113, 0.7);
}

h2 {
   font-size: 2rem;
}

h3 {
   font-size: 1.75rem;
}

h4 {
   font-size: 1.5rem;
}

h5 {
   font-size: 1.25rem;
}

h6 {
   font-size: 1rem;
}

p {
   font-size: 1.1rem;
   margin: 20px 0;
   line-height: 2rem;
}

a {
   color: white;
   color: var(--light);
   text-decoration: none;
   opacity: 0.5;
}

a:hover {
   opacity: 1;
   transition: all 0.2s ease-in-out;
}

header {
   background: #0232f4;
   background: var(--dark);
}

header a {
   color: white;
   color: var(--light);
}

button {
   font-size: 1rem;
   font-weight: 600;
   text-decoration: none;
   border-radius: 0.5rem;
   border: none;
   color: #73efef;
   outline: none;
   cursor: pointer;
   margin: 0 1px;
   background-color: #0232f4;
   background-color: var(--dark);
   padding: 10px 15px;
   opacity: 0.9;
}

button:hover {
   opacity: 1;
   -webkit-transform: scale(1.1);
           transform: scale(1.1);
}

button[disabled] {
   cursor: default;
   opacity: 0.8;
}

img {
   max-width: 100%;
}

span[role="img"] {
   cursor: default;
}

form {
   width: 40%;
}

input {
   margin: 10px 0;
   font-size: 1rem;
   padding: 0.25rem;
   width: 600px;
   border: 1px solid #0232f4;
   border: 1px solid var(--dark);
   outline: none;
   border-radius: 3px;
   transition: box-shadow 0.2s;
}

input:focus {
   border: 1px solid #7efff0;
   border: 1px solid var(--secondary);
   box-shadow: 0 0 0.25rem rgba(0, 0, 0, 0.5);
}

label {
   margin-right: 10px;
}

li {
   list-style: none;
}

header {
   height: 200px;
   position: relative;
   overflow: hidden;
   z-index: 2;
}

header h1 {
   position: relative;
   z-index: 2;
}

header img {
   z-index: 0;
   position: absolute;
   top: 0;
   left: 0;
   height: auto;
   -webkit-filter: sepia(1) opacity(90%);
           filter: sepia(1) opacity(90%);
   opacity: 0.9;
}
.cover-image {
   width: 100%;
   height: 50vh;
   object-fit: cover; /* ensure the image fills the container and is not distorted */
}

nav {
   position: relative;
   font-size: 2.5rem;
   margin: 20px 20px;
}

nav a:hover {
   text-decoration: none;
}

nav li {
   cursor: pointer;
   text-shadow: 1px 1px 5px rgb(2, 6, 113, 0.4);
   margin: 0 50px;
}

ul.skills {
   line-height: 1.5rem;
}

ul.skills li {
   list-style-type: circle;
}

footer {
   font-size: 32px;
   justify-content: center;
}

footer a {
   color: white;
   color: var(--light);
   opacity: 0.5;
   margin: 10px 20px;
   transition: all 0.7s ease-in-out;
}

footer a:hover {
   opacity: 1;
}

textarea {
   width: 600px;
   height: 200px;
   border: 3px solid #cccccc;
   padding: 5px;
   margin-top: 10px;
}

.container {
   width: 85%;
   max-width: 1200px;
   margin-left: auto;
   margin-right: auto;
}

.flex-row {
   display: flex;
   flex-wrap: wrap;
}

.space-between {
   justify-content: space-between;
}

.flex-end {
   justify-content: flex-end;
}

.flex-center {
   justify-content: center;
}

.mx-1 {
   margin-right: calc(2rem);
   margin-right: var(--spacing-two);
   margin-left: calc(2rem);
   margin-left: var(--spacing-two);
}

.mx-2 {
   margin-right: calc(2rem);
   margin-right: var(--spacing-two);
   margin-left: calc(2rem);
   margin-left: var(--spacing-two);
}

.my-1 {
   margin-top: calc(1rem * 0.8);
   margin-top: var(--spacing-one);
   margin-bottom: calc(1rem * 0.8);
   margin-bottom: var(--spacing-one);
}

.my-2 {
   margin-top: calc(2rem);
   margin-top: var(--spacing-two);
   margin-bottom: calc(2rem);
   margin-bottom: var(--spacing-two);
}

.my-5 {
   margin-top: calc(2.2rem);
   margin-top: var(--spacing-five);
   margin-bottom: calc(2.2rem);
   margin-bottom: var(--spacing-five);
}

.px-1 {
   padding-right: calc(1rem * 0.8);
   padding-right: var(--spacing-one);
   padding-left: calc(1rem * 0.8);
   padding-left: var(--spacing-one);
}

.px-2 {
   padding-right: calc(2rem);
   padding-right: var(--spacing-two);
   padding-left: calc(2rem);
   padding-left: var(--spacing-two);
}

.py-1 {
   padding-top: calc(1rem * 0.8);
   padding-top: var(--spacing-one);
   padding-bottom: calc(1rem * 0.8);
   padding-bottom: var(--spacing-one);
}

.py-2 {
   padding-top: calc(2rem);
   padding-top: var(--spacing-two);
   padding-bottom: calc(2rem);
   padding-bottom: var(--spacing-two);
}

.card {
   width: 25%;
   text-align: center;
}

.project {
   position: relative;
   width: 100%;
   max-width: 500px;
   height: 300px;
   margin: 30px;
}

.project-bg {
   display: flex;
   flex-wrap: wrap;
   position: relative;
   width: 100%;
   height: 100%;
}

.project-text {
   color: #7efff0;
   color: var(--secondary);
   position: absolute;
   top: 0px;
   bottom: 0px;
   left: 0px;
   right: 0px;
   padding: 50px;
   z-index: 900;
   opacity: 0;
   transition: all 0.5s ease-in-out;
}

.project-text:hover {
   background-color: rgba(138, 85, 85, 0.8);
   background-color: var(--alpha);
   opacity: 1;
   border: 10px solid #7efff0;
   border: 10px solid var(--secondary);
}

.project-text h3 {
   font-size: 2.5rem;
}

.project-text a {
   text-decoration: none;
   font-weight: bold;
   color: #0232f4;
   color: var(--dark);
   transition: all 0.7s ease-in-out;
}

.project-text a:hover {
   color: #010b1b;
   color: var(--darker);
}

.profile-img {
   max-width: 100px;
}

.profile-img img {
   border-radius: 10%;
   border: #175b78;
   border-style: solid;
}

.navActive {
   color: #7efff0;
   color: var(--secondary);
   cursor: default;
   text-shadow: 3px 3px 15px rgba(12, 27, 77, 0.5);
}
@media only screen and (max-width: 1300px) {
   nav {
      position: relative;
      display: block;
      z-index: 100;
      margin: 0 10px;
      font-size: 2rem;
   }
   header h1 {
      display: block;
      height: 30px;
   }
}
@media only screen and (max-width: 900px) {
   nav {
      position: relative;
      display: block;
      z-index: 100;
      margin: 0 10px;
      font-size: 1.3rem;
   }
   header h1 {
      display: block;
      margin: 5px 10px;
      height: 15px;
      width: 100%;
      text-align: center;
      font-size: 2.3rem;
   }
}

